import React from 'react';
import styled from 'styled-components';


function Login() {
    return (
        <Container>
                <CTA>
                    <CTALogoOne src='https://res.cloudinary.com/dm2gfj40h/image/upload/v1619977528/cta-logo-one_wrefrm.svg'/>
                    <SignUp>Get All There</SignUp>
                    <Description>
                    Get Premier Access to Raya and the Last Dragon for an additional fee with a Disney+ subscription. As of 03/26/21, the price of Disney+ and The Disney Bundle will increase by $1.
                    </Description>
                    <CTALogoTwo src='https://res.cloudinary.com/dm2gfj40h/image/upload/v1619977529/cta-logo-two_a0ifyi.png'></CTALogoTwo>
                </CTA>
        </Container>
    )
}

export default Login

const Container = styled.div`
    position: relative;
    height: calc(100vh - 70px);
    display: flex;
    align-items: top;
    justify-content: center;
    

    &:before{
        background-position: top;
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
        content: "";
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        opacity: 0.7;
        background-image: url('https://res.cloudinary.com/dm2gfj40h/image/upload/v1619977530/login-background_wafxme.jpg');
        z-index: -1;
    }
`

const CTA = styled.div`
    max-width: 650px;
    padding: 80px 40px;
    width: 90%;
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    align-items: center;
    
`

const CTALogoOne = styled.img`

`

const SignUp = styled.a`
    width: 100%;
    background: #0063e5;
    font-weight: bold;
    padding: 17px 0;
    color: #f9f9f9;
    border-radius: 4px;
    text-align: center;
    font-size: 18px;
    cursor: pointer;
    transition: all 250ms;
    letter-spacing: 1.5px;
    maregin-top: 8px;
    margin-bottom: 12px;

    &:hover {
        background: #0483ee;
    }
`

const Description = styled.p`
    font-size: 11px;
    letter-spacing: 1.5px;
    text-align: center;
    line-height: 1.5;
`

const CTALogoTwo = styled.img`
    width: 90%;
    
`